  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  gap: 40px;
}
@media screen and (min-width: 1280px) {
  .nav-menu {
    gap: 403px;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    margin-left: 48px;
  }
}

.nav-menu__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 16px;
  letter-spacing: 0;
}
@media screen and (min-width: 1280px) {
  .nav-menu__title {
    font-size: 24px;
    margin-bottom: 24px;
  }
}

.nav-menu__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  gap: 8px;
}
@media screen and (min-width: 1280px) {
  .nav-menu__list {
    gap: 16px;
  }
}

.nav-menu__link {
  color: rgba(33, 36, 46, 0.8);
  -webkit-transition: all 200ms linear;
  -o-transition: all 200ms linear;
  transition: all 200ms linear;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0;
}

.nav-menu__link:active {
  color: #d0bc7a;
}

.nav-menu__link:hover,
.nav-menu__link:focus {
  color: #21242e;
}